import React,{useState,useEffect} from 'react';
import {Link, useHistory} from 'react-router-dom';
import '../../asset/all.css'
import '../../asset/aptsearch.css'

export default function AptSearch1(){
    let history = useHistory();
    function goSearch(){
        if(nameSearch === ''){
            alert(' .');
        }
        else{
            history.push({
                pathname:"/search/"+nameSearch,
                state: {nameSearch:nameSearch}
            });
        }
    }
    function goSearch2(){
        if(cateSearch == false){
            alert('  .');
        }
        else{
            history.push({
                pathname:"/search/"+cateSearch,
                state:{cateSearch:cateSearch}
            });
        }
    }


    
    let [nameSearch, setNameSearch]= useState('');
    let [cateSearch, setCateSearch] = useState([]);

    let selectOptions =[
        {id:0,name:'',option:['','','']},
        {id:1,name:'',option:['','','']},
        {id:2,name:'',option:['8','9','10','11']},
        {id:3,name:'',option:['20','30','40']}
    ];


    return(
        <div className="container">
            <div className="search_info">
                  <br/>    .
            </div>
            <div className="search_container">
                    <p className="search_title">  </p>
                    <div className="search_name">
                        <input type="text" placeholder="ex)   " onChange={(e)=>{setNameSearch(e.target.value);}}/>
                        <div className="btn_container">
                             {/* <Link to={"/search:"+nameSearch}><span className="btn_search">  </span></Link> */}
                             <span className="btn_search" onClick={goSearch}>  </span>
                        </div>
                    </div>
                    <p className="search_title"> </p>
                    <div className="search_category">
                        {selectOptions.map((a, i)=>{
                            return(
                                <div className="category cate">
                                    <span>{a.name}</span>
                                    <select className="category_box" onChange={(e)=>{setCateSearch(cateSearch=>[...cateSearch, e.target.value])}}>
                                        <option value="">{a.name} .</option>
                                        {
                                            selectOptions[i].option.map((a,i)=>{
                                                return(
                                                <option value={a}>{a}</option>
                                                )
                                            })
                                        }
                                    </select>
                                </div>
                            )
                        })}
                    </div>
                    <div className="btn_container">
                    <span className="btn_search" onClick={goSearch2}>  </span>
                    </div>
            </div>
        </div>
    );
}